<script>
	import { onMount } from "svelte";
	import {scaleLinear}  from "d3-scale";

	export const myName = "Md Montaser Hamid";

	let data;
	let xScaleNew;
	let maxPopulation=9999999999;
	let maxWidth=400;
	let xScaleTicks=[];
	onMount(async () => {
		const fetched = await fetch("./static/census2000.json");
		let rawData = (await fetched.json()).demographic_groups;
		console.log(rawData);
		data = rawData.filter((record)=>{
			return record.year==2000 && record.sex=="F";
		});
		console.log("filtered ",data.length);
		let arrayOfPeople=data.map((record)=>{
			return record.people
		});
		maxPopulation=Math.max(...arrayOfPeople);
		console.log(maxPopulation);
		xScaleNew=scaleLinear()
				.domain([0, maxPopulation])
				.range([0,maxWidth]);
		xScaleTicks=xScaleNew.ticks(7)
		console.log(xScaleTicks);
	});
	function colorAge(value){
		if (value=="0-4"|| value=="5-9"|| value=="10-14"){
			return "rgb(25, 48, 92)";
		}
		else{
			return "rgb(27, 131, 27)";
		}
	}
	
	//take population value and return width of the bar
	// function xScale(value){
	// 	return (value/maxPopulation)*maxWidth;
	// }
</script>

<main>
	<h1>In-Class Acitivity by {myName}!</h1>

	<svg id="visualization" transform="rotate(-90)">
		{#if data !== undefined}
			{#each data as record, index}
				<g transform="translate(100, {index * 15 +50})">
					<rect class="bar" x="0" y="0" width={xScaleNew(record.people)} height="10" style="fill:{colorAge(record.age)}"></rect>
					<text class="x_labels" x="-40" y="8">{record.age}</text>
				</g>
			{/each}
			{#each xScaleTicks as tick}
				<g>
					<text class="y_labels" x={100+xScaleNew(tick)} y="40">{tick}</text> 
					<line class="tick"
						x1= {100+xScaleNew(tick)}
						y1="40"
						x2={100+xScaleNew(tick)}
						y2="45"
					></line>
					<line class="grid_line"
						x1= {100+xScaleNew(tick)}
						y1="40"
						x2={100+xScaleNew(tick)}
						y2={maxWidth}
					></line>
				</g>	
			
			{/each}

			<line class="tick" x1="100" y1="45" x2={maxWidth+100} y2="45"></line>
			<line class="tick" x1="100" y1="45" x2="100" y2={maxWidth}></line>
			<line class="tick" x1={maxWidth+100} y1="45" x2={maxWidth+100} y2={maxWidth}></line>
			<line class="tick" x1="100" y1={maxWidth} x2={maxWidth+100} y2={maxWidth}></line>
			<text class="title" x={maxWidth/2+70} y="15">Population</text>
			<text transform="translate(35,{maxWidth/2-50}) rotate(90)" class="title">Age Group</text>
		{/if}
	</svg>

</main>

<style>
	h1 {
		font-size: 1.5rem;
	}
	svg#visualization {
		width: 600px;
		height: 500px;
	}
	text {
		font-size: 0.6rem;
	}
	.bar{
		fill: rgb(27, 131, 27);
	}
	.tick{
		stroke: black;
	}
	.grid_line{
			stroke: grey;
  			stroke-dasharray: 1;
  			stroke-width: 1;
			fill: none;
		}
	.x_labels{
		font-size: 12px;
		font-weight: bold;
	}
	.y_labels{
		font-size: 10px;
		font-weight: bold;
	}
	.title{
		font-size: 15px;
		font-weight: bolder;
	}

</style>
